.charAdditionControl {
	white-space: nowrap;
	border: 0px;
	min-width: 150px;
	background-color: #f5f4f0;
	display: inline;
    margin-bottom: 7px;
}

.addCharButton {
	margin-top: 6px;
	margin-left: 3px;
	margin-right: 4px;
	display: inline-block;
	cursor: pointer;
	width: 13px;
	height: 13px;
	transform: scale(1.1);
}
